.banner {
  min-height: 100vh;
  overflow: hidden;
  @include media-query($mobile) {
    overflow: unset;
  }
  .cartIcon__wrapper {
    position: relative;
  }
  &__wrapper {
    min-height: inherit;
    grid-template-columns: 1fr 1fr;
    @include media-query($mobile) {
      grid-template-columns: 1fr;
      width: 100%;
    }
  }
  &__content {
    flex-direction: column;
    min-height: inherit;
    position: relative;
    width: 100%;
    &__wrapper {
      position: absolute;
      top: 50%;
      left: 70%;
      transform: translate(-50%, -50%);
      width: 100%;
      @include media-query($mobile) {
        left: 50%;
        padding: 1rem;
      }
    }

    .icon__QR {
      position: absolute;
      left: 4rem;
      top: 50%;
      font-size: 4rem;
      color: $orange;
      @include media-query($mobile) {
        display: none;
      }
    }
    .icon__cart {
      font-size: 3rem;
      color: $yellow;
    }
  }

  &__header {
    max-width: 100rem;
    font-size: clamp(6rem, 5vw, 12rem);
    font-family: "Poppins", sans-serif;
    font-weight: 900;
    line-height: 1;
    margin-bottom: 2rem;
    color: $yellow;
    text-transform: capitalize;
  }

  &__body {
    max-width: 55rem;
    margin-bottom: 3rem;
    font-size: 2rem;
    @include media-query($mobile) {
      font-size: 1.6rem;
    }
  }

  &__top {
    position: absolute;
    top: 2rem;
    left: 4rem;
    display: flex;
    justify-content: space-between;

    width: 93%;
    @include media-query($mobile) {
      left: 1.5rem;
      width: 90%;
      align-items: center;
    }
    img {
      @include media-query($mobile) {
        width: 20rem;
      }
    }
  }
  .icon_socialmedia {
    position: absolute;
    right: 2rem;
    bottom: 4rem;
    @include media-query($mobile) {
      display: flex;
    }
    li {
      margin-bottom: 2rem;
      @include media-query($mobile) {
        margin-left: 2rem;
      }
      a {
        font-size: 2.5rem;
        color: $lightgray;
      }
    }
  }

  &__img {
    @include media-query($mobile) {
      display: none;
    }
    img {
      object-fit: cover;
      display: block;
      width: 100%;
      height: 100%;
    }
  }
}

.qr__modal {
  display: none;
  justify-content: center;
  align-items: center;
  position: fixed;
  height: 100vh;
  width: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  &__main {
    max-width: 40rem;
    min-height: 40rem;
    background-color: $light;
    border-radius: 0.5rem;
    text-align: center;
    padding: 3rem;
    h3 {
      font-weight: bold;
      text-transform: capitalize;
      font-size: 2rem;
    }

    button {
      cursor: pointer;
      display: block;
      margin: 0 auto;
      padding: 1rem 2.6rem;
      border-radius: 1rem;
      border: none;
      background-color: $orange;
      color: $light;
      &:hover {
        background-color: darken($orange, 2);
      }
    }
  }
}

.badge {
  position: absolute;
  bottom: 2.5rem;
  right: -0.5rem;
  height: 2rem;
  width: 2rem;
  justify-content: center;
  align-items: center;
  background-color: red;
  border-radius: 50%;
  font-size: 1rem;
  color: $light;
  display: none;
}
.badge.show {
  display: flex;
}

.qr__modal.open {
  display: flex;
}
