.order {
  padding: 10rem 0 0;
  min-height: 100vh;
  &__grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 10rem;
    @include media-query($mobile) {
      grid-template-columns: 1fr;
    }
  }

  h2 {
    padding: 2rem;
    text-align: center;
    font-size: 2.5rem;
    margin-bottom: 2rem;
    font-weight: bold;
  }

  &__steps {
    margin-bottom: 4rem;
    display: flex;
    transition: 0.5s ease;
    &:hover {
      opacity: 1 !important;
    }
    .step__count {
      margin-right: 2rem;
      span {
        width: 4rem;
        height: 4rem;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        border: 1px solid #ccc;
        font-weight: bold;
        color: #ccc;
        background-color: $light;
        position: relative;
        z-index: 9;
        transition: 0.5s ease;
      }
    }
    .step__body {
      position: relative;
      h3 {
        font-size: 2rem;
        font-weight: bold;
        margin-bottom: 1rem;
      }
      p {
        font-size: 1.6rem;
      }

      &::before {
        content: "";
        position: absolute;
        width: 2px;
        height: 100%;
        background-color: #ccc;
        left: -4rem;
        top: 4rem;
        transition: 0.5s ease;
      }
    }

    &:hover .step__count span {
      border-color: $orange;
      background-color: $orange;
      color: $light;
    }

    &:hover .step__body h3 {
      color: $orange;
    }

    &:hover .step__body::before {
      background-color: $orange;
    }
  }

  &__img {
    text-align: center;
    img {
      width: 28rem;
    }
  }
}

.contact {
  padding: 10rem 0;
  background-color: $light-d;
  background-color: $orange;
  color: $light;
  &__grid {
    display: grid;
    grid-template-columns: 2.5fr 1fr 1fr 1fr 1fr;
    grid-gap: 5rem;
    @include media-query($mobile) {
      grid-template-columns: 1fr 1fr;
    }
  }
  &__box {
    h3 {
      font-weight: bold;
      margin-bottom: 1rem;
      text-transform: uppercase;
      font-size: 1.8rem;
    }
    p,
    li {
      font-size: 1.4rem;
    }

    .social__media {
      display: flex;
      li {
        margin-right: 1rem;
        a {
          font-size: 2.5rem;
          color: $light;
        }
      }
    }

    button {
      display: flex;
      align-items: center;
      border: none;
      background-color: transparent;
      cursor: pointer;
      color: $light;
      svg {
        font-size: 2rem;
      }

      span {
        padding-left: 1rem;
      }
    }
  }
}

.map__modal {
  display: none;
  justify-content: center;
  align-items: center;
  position: fixed;
  height: 100vh;
  width: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 99999;
  &__main {
    width: 80rem;
    max-width: 100%;

    background-color: $light;
    border-radius: 0.5rem;
    text-align: center;
    padding: 3rem;
    iframe {
      width: 100%;
      min-height: 60rem;
      border: 1px solid #ccc;
    }
  }

  h3 {
    margin-bottom: 3rem;
    font-size: 2rem;
    font-weight: bold;
  }

  button {
    border: none;
    background-color: transparent;
    margin-top: 2rem;
    cursor: pointer;
    color: $light;
    background-color: $orange;
    padding: 0.8rem 1.6rem;
    border-radius: 5px;
    transition: 0.5s ease;
    &:hover {
      background-color: darken($orange, 20);
    }
  }
}

.map__modal.open {
  display: flex;
}
